import { useRef, useEffect, useState } from "react";
import { Helmet } from 'react-helmet';
import { NavLink } from "react-router-dom";
import { MapContainer, useMap, ScaleControl, Marker, Popup } from 'react-leaflet'
import 'leaflet/dist/leaflet.css'
import BaseMap from '../components/BaseMap';
import 'leaflet-fullscreen/dist/Leaflet.fullscreen.js';
import 'leaflet-fullscreen/dist/leaflet.fullscreen.css';
import { RiskDamList } from "../data/RiskDams";
import MarkerIcon from '../assets/images/marker-icon.png'
import * as L from "leaflet";
import { setInitialMapZoom, setDragging } from "../utilities/Functions";



const myIcon = new L.Icon({
    iconUrl: MarkerIcon,
    iconRetinaUrl: MarkerIcon,
    popupAnchor: [0, -5],
    iconSize: [15],
});

const ListMarkers = ({ onItemClick }) => {
    return (
        <ul>
            {RiskDamList.map((Dam, index) => (
                <li
                    className="dam-list-item"
                    key={index}
                    onClick={(e) => {
                        e.preventDefault();
                        onItemClick(index);
                    }}
                >
                    <div className="dam-item">
                        <p className="dam-name">{Dam.properties.Name}</p>
                    </div>
                </li>
            ))}
        </ul>
    );
};

const PointMarker = ({ center, content, openPopup }) => {
    const map = useMap();
    const markerRef = useRef(null);

    useEffect(() => {
        if (openPopup) {
            map.flyTo(center, 14, {
                duration: 3
            });
            // map.flyToBounds([center]);
            markerRef.current.openPopup();
        }
    }, [map, center, openPopup]);

    return (
        <Marker ref={markerRef} position={center} icon={myIcon}>
            <Popup>
                <div className='risk-analysis-popup'>
                    <h4>{content.properties.Name}</h4>
                    <img src={content.properties.GraphImg} alt={content.properties.Name} />
                    <a href={content.properties.GraphImg} target="_blank" rel="noreferrer">Enlarge </a>
                </div>
            </Popup>
        </Marker>
    );
};

const MyMarkers = ({ data, selectedIndex }) => {
    return RiskDamList.map((Dam, index) => (
        <PointMarker
            key={index}
            content={Dam}
            center={[Dam.geometry.coordinates[1], Dam.geometry.coordinates[0]]}
            openPopup={selectedIndex === index}
        />
    ));
};

const DamRiskAnalysis = () => {
    const [selected, setSelected] = useState();

    function handleItemClick(index) {
        setSelected(index);
    }
    return (
        <>
            <Helmet>
                <title>Risk Analysis | GeoID</title>
            </Helmet>
            <div className="main-map">
                <div className="dam-list">
                    <div className="heading">
                        <h2>Indian Dams Risk Analysis</h2>
                        <button className="BackButton"><NavLink to="/projected-risk">&laquo; Back</NavLink></button>
                    </div>
                    <ListMarkers onItemClick={handleItemClick} />
                </div>
                <div className="right-side">
                    <MapContainer
                        fullscreenControl={true}
                        center={[23, 84]}
                        style={{
                            width: "100%",
                            height: "100%",
                          }}
                        zoomDelta={0.5}
                        zoomSnap={0}
                        zoom={setInitialMapZoom()}
                        dragging={setDragging()}
                        // maxZoom={8}
                        // maxBounds={[[8, 98], [38, 68]]}
                        minZoom={3}
                        scrollWheelZoom={true}>
                        <ScaleControl position="bottomleft" />

                        <BaseMap />

                        {/* {RiskDamList.map((Dam, index) => (
                            <Marker
                                position={[Dam.geometry.coordinates[1], Dam.geometry.coordinates[0]]}
                                icon={myIcon}
                            >
                                <Popup>
                                    <div className='risk-analysis-popup'>
                                        <h4>{Dam.properties.Name}</h4>
                                        <img src={Dam.properties.GraphImg} alt={Dam.properties.Name}/>    
                                        <div className="read-more">
                                            <a href={Dam.properties.GraphImg} target="_blank" rel="noreferrer">Enlarge </a>
                                        </div>
                                    </div>

                                </Popup>
                            </Marker>
                        ))
                        } */}

                        <MyMarkers selectedIndex={selected} />

                    </MapContainer>

                </div>
            </div>
        </>
    )
}

export default DamRiskAnalysis